    import React from 'react';
    import {NavLink, withRouter, Link} from 'react-router-dom';

    const Header = (props) => {
        const logout = e => {
            e.preventDefault();
            localStorage.removeItem('X-Auth-token');
            props.history.push('/login');
        }
        
        return (
            <>
                <div className="navbar navbar-inverse navbar-fixed-top" style={{minHeight: '65px'}}>
                    <div className="adjust-nav">
                        <div className="navbar-header">
                            <button type="button" className="navbar-toggle" data-toggle="collapse" data-target=".sidebar-collapse">
                                <span className="icon-bar"></span>
                                <span className="icon-bar"></span>
                                <span className="icon-bar"></span>
                            </button>
                            <span className="navbar-brand text-white">
                                {/* <img src="assets/img/logo.png" /> */}
                                <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                    <i className='fa fa-android' style={{color: '#66ff33'}}></i>
                                    <span className='lead' style={{color: "#fff", margin: '0', marginLeft: '5px'}}> Paul the bot</span>
                                </div>
                                
                                
                            </span>
                            
                        </div>
                    
                        <span className="logout-spn" >
                        <Link className='btn btn-sm' onClick={e => logout(e)} style={{color: "#fff"}}>LOGOUT</Link>  

                        </span>
                    </div>
                </div>
                <nav className="navbar-default navbar-side" role="navigation">
                    <div className="sidebar-collapse">
                        <ul className="nav text-left" id="main-menu">
                        


                            <li className="active-link">
                                <NavLink to="/" ><i className="fa fa-desktop "></i>Dashboard</NavLink>
                            </li>
                        

                            <li>
                                <NavLink to="/password"><i className="fa fa-lock "></i>Change password</NavLink>
                            </li>
                            <li>
                                <NavLink to="/logs"><i className="fa fa-edit "></i>Logs <span className="badge bg-danger">new</span></NavLink>
                            </li>


                            <li>
                                <NavLink to="/preferences"><i className="fa fa-qrcode "></i>Preferences</NavLink>
                            </li>
                            
                            <li className='logOutNavItem'>
                                <NavLink to="" onClick={e => logout(e)}><i className="fa fa-sign-out "></i>Log out</NavLink>
                            </li>
                        </ul>
                    </div>

                </nav>
            </>
        );
    };

    export default withRouter(Header);