import {lazy} from 'react';


const Home = lazy(() => import("../Containers/Home/Home"));
const Login = lazy(() => import("../Containers/Login/Login"));
const Password = lazy(() => import("../Containers/Password/Password"));
const Preferences = lazy(() => import("../Containers/Preferences/Preferences"));
const Logs = lazy(() => import("../Containers/Logs/Logs"));
// const Terms = lazy(() => import("../Containers/Terms/Terms"));
// const Privacy = lazy(() => import("../Containers/Privacy/Privacy"));


const routes = [
    {
        path: '/',
        exact: true,
        component: Home
    },
    {
        path: '/login',
        exact: true,
        component: Login
    },
    {
        path: '/password',
        exact: true,
        component: Password
    },
    {
        path: '/preferences',
        exact: true,
        component: Preferences
    },
    {
        path: '/logs',
        exact: true,
        component: Logs
    }
]


export default routes;