import React, {useEffect, useState, lazy} from 'react';
import './App.css';
import Header from './Components/Header/Header';
import Footer from './Components/Footer/Footer';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import routes from './utils/routes';
import {Suspense} from 'react';
import PricingContext from './context/pricingContext';
const NotFound = lazy(() => import('./Containers/NotFound/NotFound'));

function App() {

  return (
    <Router>
      <div className="App">
        <Header />
        <div className="main-content-wrapper">            
          <Suspense fallback={
          <section data-settings="particles-1" className="main-section crumina-flying-balls particles-js bg-1 medium-padding120 responsive-align-center">
            <div className="lds-spinner" style={{margin: 'auto'}}><div></div><div></div></div>
          </section>}>
              <Switch>
                {
                routes.map(route => {
                  return <Route path={route.path} exact={route.exact} component={route.component} key={route.path} />
                })
                }
                <Route path='*' component={NotFound} />
              </Switch>
            
          </Suspense>
        </div>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
